const app=getApp()
export default {
	data() {
		return {
			list: [],
			phone_number:undefined
		};
	},
	onLoad() {
		var that = this;
		this.getBasicInfo();
		uni.request({
			url: app.globalData.URL+'getArticles',
			success(res) {
				console.log(res);
				for (var i = 0; i < res.data.item.length; i++) {
					console.log(res.data.item[i].content.news_item[0]);
					that.list.push({ image: res.data.item[i].content.news_item[0].thumb_url, title: res.data.item[i].content.news_item[0].title });
				}
				console.log(that.list);
			}
		});
	},
	methods: {
		jumpweb(){
			getApp().globalData.articleurl='https://www.lzhcqc.com'
			uni.navigateTo({
				url:'article'
			})
		},
		guanzhu(){
			getApp().globalData.articleurl='https://mp.weixin.qq.com/s/plFv63CEUc1AJgFwUDKhtg'
			uni.navigateTo({
				url:'article',
			})
		},
		makephonecall(){
			uni.makePhoneCall({
				phoneNumber:'18042853923'
			})
		},
		start(e) {
			var that = this;
			if (uni.getStorageSync('phone_number')){
				this.makePhonecall()
				return
			}
			uni.checkSession({
				success() {
					that.decryptPhoneNumber(that.session_key, e.detail.encryptedData, e.detail.iv);
				},
				fail() {
					uni.login({
						success(res) {
							uni.showLoading({
								title: '加载中'
							});
							uni.request({
								url: getApp().globalData.URL + 'getBasicInfo',
								header: {
									'content-type': 'application/x-www-form-urlencoded'
								},
								method: 'POST',
								data: { code: res.code },
								success(res) {
									console.log(res);
									that.session_key = res.data.session_key;
									that.decryptPhoneNumber(that.session_key, e.detail.encryptedData, e.detail.iv);
								},
								fail(res) {
									uni.showToast({
										title: '网络错误'
									});
								},
								complete() {
									uni.hideLoading();
								}
							});
						}
					});
				}
			});
		},
		decryptPhoneNumber(session_key, encryptedData, iv) {
			var that = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: getApp().globalData.URL + 'getPhoneNumber',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				data: {
					session_key: session_key,
					encryptedData: encryptedData,
					iv: iv
				},
				success(res) {
					console.log(res);
					uni.setStorageSync('phone_number', res.data);
					getApp().globalData.phone_number = res.data;
					that.phone_number = res.data;
					console.log(res.data);
					that.makephonecall()
				},
				fail(res) {
					console.log(res);
					uni.showToast({
						title: '网络错误'
					});
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		getBasicInfo() {
			var that = this;
			uni.login({
				success(res) {
					uni.showLoading({
						title: '加载中'
					});
					uni.request({
						url: getApp().globalData.URL + 'getBasicInfo',
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						method: 'POST',
						data: { code: res.code },
						success(res) {
							console.log(res);
							that.session_key = res.data.session_key;
							that.openid = res.data.openid;
							uni.setStorageSync('openid', res.data.openid);
							getApp().globalData.openid = res.data.openid;
						},
						fail(res) {
							console.log(res);
							uni.showToast({
								title: '网络错误'
							});
						},
						complete() {
							uni.hideLoading();
						}
					});
				}
			});
		},
		attend_aci(){
			uni.showToast({
				title:'活动筹备中',
				icon:'none'
			})
		}
	}
};
